import React, {useState} from 'react';

const FuckingShit = props => {
    const [state, setState] = useState();
    if(props.text) {
        let a = props.text.split('\n')[0].replace(/<(.|\n)*?>/g, '').slice(0, 140);
        if (a[a.length - 1] === " " || a[a.length - 1] === "." || a[a.length - 1] === "!" || a[a.length - 1] === "?" || a[a.length - 1] === ",") {
            a = a.slice(0, a.length - 1)
        } else if (a[a.length - 2] === " " || a[a.length - 2] === "." || a[a.length - 2] === "!" || a[a.length - 2] === "?" || a[a.length - 2] === ",") {
            a = a.slice(0, a.length - 2)
        }
        return a + "..."
    }
    return "";
}

export default FuckingShit;