import React from 'react';
import './Persik.css';


import {
    Button,
    Panel,
    Spinner,
    Gallery, Search, FixedLayout, PanelHeaderButton, PanelHeader

} from '@vkontakte/vkui';


import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon28MoonOutline from '@vkontakte/icons/dist/28/moon_outline';
import logo from "../img/logo.svg";
import logo2 from "../img/logo_d.svg";
import Carousel, {Modal, ModalGateway} from "react-images";
import Icon36LogoVk from '@vkontakte/icons/dist/36/logo_vk';
import Icon24LogoGoogle from '@vkontakte/icons/dist/24/logo_google';
import yt from "../img/yt.svg";





const Article = props => (
    <Panel id={props.id}>
        {document.body.clientWidth < 992 ?
            <PanelHeader
                separator={false}
                left={<PanelHeaderButton  mode="tertiary" data-story="home" onClick={(e)=>props.onStoryChange(e)} ><img src={props.state.scheme2 === 'bright_light' ? logo : logo2} alt="" /></PanelHeaderButton>}
                right={<PanelHeaderButton  mode="tertiary" onClick={()=>props.shem()}><Icon28MoonOutline /></PanelHeaderButton>}
            >
                <Search
                    onChange={(e)=>props.getSea(e.target.value)}
                    value={props.state.text}
                    after={null}
                />

            </PanelHeader>
            :
            <FixedLayout vertical="top" className="menu1">
                <div style={{backgroundImage: 'url('+(props.state.scheme2 === 'bright_light' ? logo : logo2)+')'}} className="logo" data-story="home" onClick={(e)=>props.onStoryChange(e)}>
                </div>
                <div className="menu2">
                    <Button mode="tertiary" data-story="home" onClick={(e)=>props.onStoryChange(e)}>Главная</Button>
                    <Button mode="tertiary" data-story="articles" onClick={(e)=>props.onStoryChange(e)}>Все статьи</Button>
                    <Button mode="tertiary" data-story="galley" onClick={(e)=>props.onStoryChange(e)}>Галерея</Button>
                    <Button mode="tertiary" data-story="shop" onClick={(e)=>props.onStoryChange(e)}>Товары</Button>
                    <Button mode="tertiary" data-story="about" onClick={(e)=>props.onStoryChange(e)}>О нас</Button>
                    <Button mode="tertiary" onClick={()=>props.shem()}><Icon28MoonOutline /></Button>
                    <Search  after={null} onChange={(e)=>props.getSea(e.target.value)} value={props.state.text} />
                </div>
            </FixedLayout>
        }

        {props.state.articles && props.state.cur_article ?

            <div>

                <ModalGateway>
                    {props.state.open ? (
                        <Modal onClose={()=>props.setState({open: false})}>
                            <Carousel
                                currentIndex={props.state.ii}
                                views={props.state.photos2.map(x => ({
                                    ...x,
                                    srcset: x.src,
                                    caption: ''
                                }))}
                            />
                        </Modal>
                    ) : null}
                </ModalGateway>

                <div className="page_3_block">

                    {document.body.clientWidth > 992 ?
                        <Button className="btn_2" mode="outline" data-story="home"
                                onClick={(e) => props.onStoryChange(e)}
                                before={<Icon24BrowserBack width={16} height={16}/>}>Главная</Button>
                        :
                        ''
                    }

                    <h2>{props.state.cur_article.ts}</h2>
                    <h1>{props.state.cur_article.name}</h1>

                    {
                        props.state.typePost === 'text' ? props.state.cur_article.photos.length > 1 ?
                        <div style={{display: 'flex', position: 'relative'}}>
                            <Button className="slidBtn1" mode="tertiary" onClick={() => props.setState({slideIndex: props.state.slideIndex === 0 ? props.state.cur_article.photos.length-1 : props.state.slideIndex - 1 })}><Icon24BrowserBack/></Button>
                            <div style={{display: 'flex', flexDirection: 'column',width: '100%'}}>
                                <Gallery
                                    slideWidth="100%"
                                    align="center"
                                    style={{ height: 'calc(50vh + 50vw / 2.28)' }}
                                    slideIndex={props.state.slideIndex}
                                    onChange={slideIndex => props.setState({slideIndex})}
                                >
                                    {props.state.cur_article.photos.map((photo,key) =>
                                            <div key={key} className="page_3_card_img" onClick={()=>props.setState({open: true,ii: key})}>
                                                <div style={{backgroundImage: 'url('+photo+')'}} className="page_3_card_img_img">
                                                </div>
                                            </div>
                                        )}

                                </Gallery>
                            </div>
                            <Button className="slidBtn2" mode="tertiary" onClick={() => props.setState({slideIndex: props.state.slideIndex === props.state.cur_article.photos.length-1 ? 0 : props.state.slideIndex + 1 })}><Icon24BrowserForward/></Button>
                        </div>

                        :
                        <div className="page_3_card_img" onClick={()=>props.setState({open: true,ii: 0})} style={{cursor: 'pointer'}} >
                            <div style={{backgroundImage: 'url('+props.state.cur_article.photos[0]+')'}} className="page_3_card_img_img">
                            </div>
                        </div>
                        :
                            props.state.cur_article.youtube !== "null_str" ? <iframe style={{width: '100%', height: 'calc(25vw + 25vh)'}}  src={props.state.cur_article.youtube}
                                frameBorder="0"
                                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen ></iframe> :
                                props.state.cur_article.photos.length > 1 ?
                                    <div style={{display: 'flex', position: 'relative'}}>
                                        <Button className="slidBtn1" mode="tertiary" onClick={() => props.setState({slideIndex: props.state.slideIndex === 0 ? props.state.cur_article.photos.length-1 : props.state.slideIndex - 1 })}><Icon24BrowserBack/></Button>
                                        <div style={{display: 'flex', flexDirection: 'column',width: '100%'}}>
                                            <Gallery
                                                slideWidth="100%"
                                                align="center"
                                                style={{ height: 'calc(50vh + 50vw / 2.28)' }}
                                                slideIndex={props.state.slideIndex}
                                                onChange={slideIndex => props.setState({slideIndex})}
                                            >
                                                {props.state.cur_article.photos.map((photo,key) =>
                                                    <div key={key} className="page_3_card_img" onClick={()=>props.setState({open: true,ii: key})}>
                                                        <div style={{backgroundImage: 'url('+photo+')'}} className="page_3_card_img_img">
                                                        </div>
                                                    </div>
                                                )}

                                            </Gallery>
                                        </div>
                                        <Button className="slidBtn2" mode="tertiary" onClick={() => props.setState({slideIndex: props.state.slideIndex === props.state.cur_article.photos.length-1 ? 0 : props.state.slideIndex + 1 })}><Icon24BrowserForward/></Button>
                                    </div>

                                    :
                                    <div className="page_3_card_img" onClick={()=>props.setState({open: true,ii: 0})} style={{cursor: 'pointer'}} >
                                        <div style={{backgroundImage: 'url('+props.state.cur_article.photos[0]+')'}} className="page_3_card_img_img">
                                        </div>
                                    </div>
                    }
                    <br />


                    <div className="page_3_text" style={{whiteSpace: 'break-spaces'}} dangerouslySetInnerHTML={props.createMarkup(props.state.cur_article.text)}>
                        {/*{props.state.cur_article.text}*/}
                    </div>
                    <Button mode="tertiary" onClick={()=>props.getSea(props.state.cur_article.tag, true)}>{props.state.cur_article.tag}</Button>

                </div>
            </div>

            :
            <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                <Spinner size="large" style={{ marginTop: '40vh' }} />
            </div>
        }


        {props.state.snackbar}
        <div className="footer">
            <div className="fot">
                <div style={{display: 'flex', alignItems: 'center'}}>
                    <div style={{backgroundImage: 'url('+(props.state.scheme2 === 'bright_light' ? logo : logo2)+')'}} className="logo2" data-story="home" onClick={(e)=>props.onStoryChange(e)}>
                    </div>

                </div>
                <div style={{paddingLeft: '3vw'}}>


                    <Button mode="tertiary" data-story="home" onClick={(e)=>props.onStoryChange(e)}>Главная</Button>
                    <Button mode="tertiary" data-story="articles" onClick={(e)=>props.onStoryChange(e)}>Все статьи</Button>
                    <Button mode="tertiary" data-story="galley" onClick={(e)=>props.onStoryChange(e)}>Галерея</Button>
                    <Button mode="tertiary" data-story="shop" onClick={(e)=>props.onStoryChange(e)}>Товары</Button>
                    <Button mode="tertiary" data-story="about" onClick={(e)=>props.onStoryChange(e)}>О нас</Button>
                </div>
                <div>
                    <Button mode="tertiary" href="https://vk.com/dimon_i_pumba_youtube" target="_blank" style={{cursor:'pointer'}}><Icon36LogoVk /></Button>
                    <Button mode="tertiary" href="https://www.youtube.com/channel/UCv1NIWA9x4DXV3VmjHSUNSA" target="_blank" style={{cursor:'pointer'}} className="sds">
                        <img src={yt} alt="" />
                    </Button>

                </div>
            </div>

            <small>© dimonipumba.ru 2020 г.</small><br />
            <small>Переписывание материалов сайта без разрешения администрации запрещено.</small>
        </div>


    </Panel>
);

export default Article;
